<template>
  <section :class="{light: isLight}" class="body">
    <Header></Header>
    <transition mode="out-in" name="pop" appear>
        <button v-if="isLight" @click="toggleDarkMode" class="btn btn-secondary toggle-dark">
          <i class="bi bi-moon-fill"></i>
        </button>
        <button v-else @click="toggleDarkMode" class="btn btn-secondary toggle-dark">
          <i class="bi bi-brightness-high-fill"></i>
        </button>
    </transition>
    <div class="body">
        <div class="row">
            <transition mode="out-in" name="pop" appear>
                <div class="col-12">
                    <LoadingSpinner v-if="$store.getters.isLoading & !$store.getters.dataLoaded"></LoadingSpinner>
                </div>
            </transition>
            <div class="d-block d-lg-none col-2"></div>
            <transition mode="out-in" name="pop" appear>
                <div class="col-12 col-md-8 col-xl-4" v-if="!this.checkIsError.error && !$store.getters.isLoading && !$store.getters.dataLoaded">
                    <div class="form-card mb-2 p-4">
                        <div class="col-12">
                            <Search @submit-form="loadData"></Search>
                        </div>
                    </div>
                    <transition mode="out-in" name="pop" appear>
                        <Alert v-if="!$store.getters.dataLoaded && !$store.getters.isLoading && $store.getters.checkIsDev" @click="loadData"/>
                    </transition>
                    <div class="d-block d-lg-none col-2"></div>
                </div>
            </transition>
            <transition mode="out-in" name="pop" appear>
                <div class="col-12" v-if="!$store.getters.dataLoaded && this.checkIsError.error || $store.getters.dataLoaded && !$store.getters.isLoading && this.$store.getters.loadData['counter'] != ''">
                    <dividend-output :data="FilteredResult" :error="this.checkIsError.error"></dividend-output>
                </div>
            </transition>     
        </div>
    </div>
    <Footer></Footer>
  </section>
</template>

<script>
import Header from './pages/Header.vue';
import Footer from './pages/Footer.vue';
import Alert from './components/UI/Alert.vue';
import Search from './components/UI/Search.vue';
import DividendOutput from './pages/DividendOutput.vue';
import LoadingSpinner from './components/UI/LoadingSpinner.vue';

export default {
    name: 'SavaJava Global Stocks Dividend Reliability & Analysis Toolkit',
    components: {
        Header,
        Footer,
        Alert,
        Search,
        DividendOutput,
        LoadingSpinner,
    },
    data(){
        return {
            FilteredResult: [],
            isError: null,
        }
    },
    mounted(){
        this.statusReport();
    },
    updated(){
        this.statusReport();
    },
    computed: {
        isLight(){
            return this.$store.getters.checkIsLight;
        },
        isDev(){
            return this.$store.getters.checkIsDev;
        },
        getData(){
            return this.$store.getters.loadData;
        },
        isLoading(){
            return this.$store.getters.isLoading;
        },
        dataLoaded(){
            return this.$store.getters.dataLoaded;
        },
        checkIsError(){
            return this.$store.getters.isError;
        },
    },
    methods: {
        statusReport(){
            this.isError = this.$store.getters.isError;
            console.log(this.checkIsError)
        },
        toggleDarkMode(){
            let body = document.querySelector('body');
            this.$store.dispatch('toggleLightMode');
            body.classList.toggle('light');
        },
        loadData(){
            if( this.$store.getters.loadData.status != 404 ){
                if(this.$store.getters.loadData[0]["Stock Type"]==="Non-Dividend"){
                    return this.FilteredResult = this.$store.getters.loadData[0];
                }
                if(this.$store.getters.loadData["Stock Type"]==="Dividend"){
                    return this.FilteredResult = this.$store.getters.loadData;
                }
            } else {
                return this.FilteredResult = this.$store.getters.loadData;
            }
        },
    },
}
</script>

